import React, { useState, useEffect } from 'react'
import Persons from './components/Person'
import Filter from './components/Filter'
import PersonForm from './components/PersonForm'
import personService from './services/persons'
import Notification from './components/Notification'
import ErrorNotification from './components/ErrorNotification'

const App = () => {

    useEffect(() => {
        personService
            .getAll()
            .then(response => {
                setPersons(response.data)
            })
    }, [])
    


    const [ persons, setPersons] = useState([]) 
    const [ newName, setNewName ] = useState('')
    const [ newNumber, setNewNumber ] = useState('')
    const [ filter, setFilter] = useState('')
    const [ message, setMessage] = useState(null)
    const [errorMessage, setErrorMessage] = useState(null)

    const nameExists = (persons, name) => {
      return (
          persons.filter(person => person.name === name).length > 0
      )
  }

  const addNameNumber = (event) => {
    event.preventDefault()

    if(nameExists(persons, newName)) {
        const accepted = window.confirm(`${newName} is already added to phonebook, replace the old number with a new one?`)
        if (accepted) {
            updateNumber(newName)
        }
    } else {
        console.log('name', newName)
        console.log('num', newNumber)
        const newPerson = {
            name: newName,
            number: newNumber
        }
    
        personService
            .create(newPerson)
            .then(newPerson => {
                console.log('set person', newPerson)
                setPersons(persons.concat(newPerson.data))
                setNewName('')
                setNewNumber('')
                setMessage(`Added ${newName}`)
                setTimeout(() => {
                    setMessage(null)
                }, 5000)
            })
            .catch(error => { 
                console.log('mitaaaa')
                console.log(error)
                setErrorMessage(`${error.response.data.error}`)
                setTimeout(() => {
                    setErrorMessage(null)
                }, 5000)
                console.log(`${error.response.data.error}`)
            })
        
    } 
  }

  const handleDelete = deletedPerson => {
      const accepted = window.confirm(`Delete ${deletedPerson.name}?`)
      if (accepted) {
          personService
            .deletePerson(deletedPerson.id)
            .then(() => {
                setPersons(persons.filter(person => person.id !== deletedPerson.id))
                setMessage(`Deleted ${deletedPerson.name} succesfully!`)
                setTimeout(() => {
                    setMessage(null)
                }, 5000)
            })
            .catch(error => {
                setErrorMessage(
                    `Information of ${deletedPerson.name} was already removed from server`
                )
                setTimeout(() => {
                    setErrorMessage(null)
                }, 5000)
            })
      }
  }

  const updateNumber = (name) => {
      const old = persons.find(person => person.name === name)
      const updated = {...old, number: newNumber}

      personService
        .update(updated.id, updated)
        .then(response => {
            setPersons(persons.map(person => 
                person.id !== old.id ? person: response.data))
            setMessage(`Number of ${name} changed succesfully!`)
            setTimeout(() => {
                setMessage(null)
            }, 5000)    
            setNewName('')
            setNewNumber('')
        })
        .catch(error => {
            setErrorMessage(
                `Information of ${name} was already removed from server`
            )
            setTimeout(() => {
                setErrorMessage(null)
            }, 5000)
        })
  }

  const handleNameChange = (event) => {
      setNewName(event.target.value)
  }

  const handleNumberChange = (event) => {
      setNewNumber(event.target.value)
  }

  const handleFilterChange = (event) => {
      setFilter(event.target.value)
  }

  return (
    <div>
      <h2>Phonebook</h2>
      <Notification message={message}/>
      <ErrorNotification message={errorMessage}/>

      <Filter filter={filter} onFilterChange={handleFilterChange}/>

      <h3>Add a new</h3>

      <PersonForm 
        addNameNumber={addNameNumber}
        handleNameChange={handleNameChange}
        handleNumberChange={handleNumberChange}
        newName={newName}
        newNumber={newNumber}
      />
      <h3>Numbers</h3>
      <Persons persons={persons} filter={filter} onDelete={handleDelete}/>
    </div>
  )
}

export default App